.App {
  text-align: center;
  outline-style: none;
}

.App-logo {
  height:2.6em;
  margin-right:0.3em;
}

.App-header {
  background-color: #222;
  padding: 2em;
  color: white;
  width:100%;
}

#add-portfolio::before {
  position:absolute;
  left:0.25em;
  top:0.35em;
  content:"\2795";
  color:#eeeeee;
  font-size:160%;
  margin-right: 8px;
  line-height: 0.8;
}
#add-portfolio {
  position:relative;
  margin-right:auto;
  margin-left:0;
  padding-left:40px;
  -ms-flex-item-align: start;
      align-self: flex-start;
  z-index:10;
  /*  box-shadow: 0 5px #006cc0;*/
}
.App-title {
  font-size: 1.2em;
  margin:auto;
  -ms-flex-positive:1;
      flex-grow:1;
}

.contents {
  padding-bottom:3em;
  display:inline-block;
  width:100%;
}

.content-row {
  -ms-flex-line-pack:stretch;
      align-content:stretch;
}
.footer {
  position:fixed;
  bottom:0;
  background-color:#222;
  width:100%;
}

.footer * {
  font-size:80%;
  -ms-flex-line-pack:baseline;
      align-content:baseline;
  color:#aaa;
  padding:0.3em;
}

.add-div {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-wrap:wrap-reverse;
      flex-wrap:wrap-reverse;
  margin:auto;
  -ms-flex:1 1;
      flex:1 1;
}
.logo-div {
  -ms-flex:0 0 60px;
      flex:0 0 60px;
}
@media (max-width:340px) {
  .App-title {
    -ms-flex-item-align:center;
        align-self:center;
  }
}

@media (max-width:443px) {
  .App-header {
    padding:0.3em;
  }
}
@media (max-width:728px) {
  #add-portfolio {
      margin:auto;
  }
}



